<template>
    <div id="app">
        <!-- <lottieLoading v-if='showAni'></lottieLoading> -->
        <router-view class="child-view" ref="routerView" :key='new Date().getTime()'></router-view>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
