            'isLogin',
            'user',
            'progress'
        ]),
    },
    watch: {
    },

}
</script>

<style lang="less">
@import "./assets/common.less";
@import "assets/css/animate.css";
@font-face {
    /* font-properties */
    font-family: MicrosoftYaqiHeiLight;
    src: url("./assets/font/MicrosoftYaqiHeiLight-2.ttf");
    /* IE9 */
}
@font-face {
    /* font-properties */
    font-family: Cousine;
    src: url("./assets/font/Cousine-Regular-1.ttf");
    /* IE9 */
}
