}
@font-face {
    /* font-properties */
    font-family: consola;
    src: url("./assets/font/consola-1.ttf");
    /* IE9 */
}
* {
    font-family: consola, OfficeCode, MicrosoftYaqiHeiLight;
}
#app {
    height: 100%;
    position: relative;
}

.el-progress {
    position: absolute !important;
    height: 8px !important;
    z-index: 99999 !important;
    width: 100% !important;
    top: -8px;
}
</style>



